header {
    background-color: $bgColor;
    padding: 1.6rem;
    
    nav {
        color: white;
        font-size: 1.2rem;

        a {
            position: relative;

            &.active {
                color: white;
            }
        }

        .star {
            font-size: 1rem;
            color: $starColor;
            position: absolute;
            bottom: 72%;
            left: 41%;
        }
    }
}