@media (min-width: 400px) {
    .container {
        padding: 1rem;
    }

    .filter {
        form {
            grid-gap: 2rem;

            .title, .year {
                width: 90%;
            }
        }
    }
}

@media (min-width: 500px) {
    .movie-display {
        grid-template-areas: 
        "header header header"
        "poster content content"
        "poster content content"
        "poster rating rating";
    }
}

@media (min-width: 586px) {
    html {
        font-size: 14px;
    }

    .filter {
        form {
            grid-gap: 2rem;

            .title, .year {
                width: 80%;
            }
        }
    }
}

@media (min-width: 768px) {
    html {
        font-size: 16px;
    }

    .movie-display {
        grid-auto-columns: 1fr;
    }
}