* {
    box-sizing: border-box;
}

html, body, h1, h2, h3, p, ul, li {
    margin: 0;
    padding: 0;
}

html {
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    height: 100%;
}

body {
    height: 100%;
    background-color: lighten($bgColor, 77%);
}

ul, li {
    list-style: none;
}

a {
    text-decoration: none;
}

h1, h2 {
    color: $hColor;
}