.movie-display {

    visibility: hidden;

    &.show {
        visibility: visible;
    }

    display: grid;
    grid-gap: .8rem;
    grid-template-areas: 
    "header header"
    "poster poster"
    "content content"
    "rating rating";

    .movie-header {
        grid-area: header;
        display: flex;
        flex-direction: column;
        padding: 0.5rem;
        border-bottom: 1px solid $greyColor;

        h2 {
            font-size: 1.2rem;
            margin-bottom: 1rem;
        }
        div {
            display: flex;
            justify-content: space-between;
        }
    }

    .movie-poster {
        grid-area: poster;
        max-width: 18.75rem;
        max-height: 447px;
        justify-self: center;
    }

    .movie-content {
        grid-area: content;
        padding: 0.5rem;
        display: flex;
        flex-direction: column;

        div, p:not(:first-child) {
            margin-top: 2rem;
        }

        .website {
            a {
                color: black;
                transition: .3s;
    
                &.active {
                    color: black;
                }
    
                &:hover {
                    color: $hColor;
                }
            }
        }
    }

    .movie-rating {
        grid-area: rating;
        align-self: flex-end;
        border-top: 1px solid $greyColor;
        padding-top: 10px;
        display: flex;
        align-items: flex-end;
        font-size: .9rem;

        .rating {
            display: flex;
            flex: 1;
            align-items: flex-end;

            .stars {
                margin-left: .4rem;
                font-size: 1rem;
                .yellow {
                    color: $starColor;
                }
    
                .nuetral {
                    color: grey;
                }
            }
        }
    }
}