.filter {
    form { 
        display: grid;
        grid-gap: 1rem;
        grid-template-areas: 
        "title year"
        "search clear";

        .title, .year {
            grid-area: "title";
            width: 95%;
            justify-self: center;
        }

        .search, .clear {
            grid-area: "search";
            width: 10.7rem;
            padding: 10px 0;
        }

        .search {
            justify-self: flex-end;
        }

        .clear {
            justify-self: flex-start;
        }

        .material-icons {
            margin-left: .8rem;
        }

        .red {
            color: $deepOrange;
        }

        .neutral {
            color: lighten($bgColor, 77%);
        }
    }
}