@font-face {
  font-family: 'Montserrat';
  src: url("./assets/fonts/Montserrat-Regular.ttf"); }

.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 0.2rem; }

.center {
  display: flex;
  align-items: center;
  justify-content: center; }

* {
  box-sizing: border-box; }

html, body, h1, h2, h3, p, ul, li {
  margin: 0;
  padding: 0; }

html {
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  height: 100%; }

body {
  height: 100%;
  background-color: #f6fbff; }

ul, li {
  list-style: none; }

a {
  text-decoration: none; }

h1, h2 {
  color: #003c6c; }

header {
  background-color: #003c6c;
  padding: 1.6rem; }
  header nav {
    color: white;
    font-size: 1.2rem; }
    header nav a {
      position: relative; }
      header nav a.active {
        color: white; }
    header nav .star {
      font-size: 1rem;
      color: #d3b300;
      position: absolute;
      bottom: 72%;
      left: 41%; }

.home-page h1 {
  text-align: center;
  font-size: 1.8rem;
  margin: 1.2rem 0 1rem; }

.home-page .filter {
  margin-bottom: 1.5rem; }

.home-page .error-display {
  margin-top: 3rem; }

.filter form {
  display: grid;
  grid-gap: 1rem;
  grid-template-areas: "title year" "search clear"; }
  .filter form .title, .filter form .year {
    grid-area: "title";
    width: 95%;
    justify-self: center; }
  .filter form .search, .filter form .clear {
    grid-area: "search";
    width: 10.7rem;
    padding: 10px 0; }
  .filter form .search {
    justify-self: flex-end; }
  .filter form .clear {
    justify-self: flex-start; }
  .filter form .material-icons {
    margin-left: .8rem; }
  .filter form .red {
    color: #bf360c; }
  .filter form .neutral {
    color: #f6fbff; }

.movie-display {
  visibility: hidden;
  display: grid;
  grid-gap: .8rem;
  grid-template-areas: "header header" "poster poster" "content content" "rating rating"; }
  .movie-display.show {
    visibility: visible; }
  .movie-display .movie-header {
    grid-area: header;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    border-bottom: 1px solid #9f9f9f; }
    .movie-display .movie-header h2 {
      font-size: 1.2rem;
      margin-bottom: 1rem; }
    .movie-display .movie-header div {
      display: flex;
      justify-content: space-between; }
  .movie-display .movie-poster {
    grid-area: poster;
    max-width: 18.75rem;
    max-height: 447px;
    justify-self: center; }
  .movie-display .movie-content {
    grid-area: content;
    padding: 0.5rem;
    display: flex;
    flex-direction: column; }
    .movie-display .movie-content div, .movie-display .movie-content p:not(:first-child) {
      margin-top: 2rem; }
    .movie-display .movie-content .website a {
      color: black;
      transition: .3s; }
      .movie-display .movie-content .website a.active {
        color: black; }
      .movie-display .movie-content .website a:hover {
        color: #003c6c; }
  .movie-display .movie-rating {
    grid-area: rating;
    align-self: flex-end;
    border-top: 1px solid #9f9f9f;
    padding-top: 10px;
    display: flex;
    align-items: flex-end;
    font-size: .9rem; }
    .movie-display .movie-rating .rating {
      display: flex;
      flex: 1;
      align-items: flex-end; }
      .movie-display .movie-rating .rating .stars {
        margin-left: .4rem;
        font-size: 1rem; }
        .movie-display .movie-rating .rating .stars .yellow {
          color: #d3b300; }
        .movie-display .movie-rating .rating .stars .nuetral {
          color: grey; }

@media (min-width: 400px) {
  .container {
    padding: 1rem; }
  .filter form {
    grid-gap: 2rem; }
    .filter form .title, .filter form .year {
      width: 90%; } }

@media (min-width: 500px) {
  .movie-display {
    grid-template-areas: "header header header" "poster content content" "poster content content" "poster rating rating"; } }

@media (min-width: 586px) {
  html {
    font-size: 14px; }
  .filter form {
    grid-gap: 2rem; }
    .filter form .title, .filter form .year {
      width: 80%; } }

@media (min-width: 768px) {
  html {
    font-size: 16px; }
  .movie-display {
    grid-auto-columns: 1fr; } }
